.cap-parent {
  display: flex;
  flex-direction: row;
}

.cap-container {
  padding: 0 2% 2% 2%;
  flex-grow: 1;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 50px;
  margin: 1% 1%;
}

.field-container {
  box-sizing: border-box;
  border-radius: 5px;
  background-color: #4a7dfc;
  margin: 20px 30px 40px 0;
}

.field-title {
  display: flex;
  justify-content: left;
  padding: 10px 0 10px 40px;
}

.field-content {
  padding: 30px;
  background-color: white;
  border-radius: 0 0 5px 5px;
  border: 0.5px solid #212529;
}

.page-container{
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.cap-form {
  border-radius: 5px;
  overflow-y: scroll;
  height: 78vh;
}

.cap-form form {
  border-radius: 5px;
  padding: 0 0 5% 0;
  background-color: white;
}

