.cap-form::-webkit-scrollbar {
  height: 100%;
  width: 16px;
  background-color: transparent;
}

.cap-form::-webkit-scrollbar-corner{
  background: black;
}

.cap-form::-webkit-scrollbar-thumb{
  background: rgba(168, 168, 168, 0.5);
  border: 1px solid rgba(168, 168, 168, 0.5);
}
.cap-form::-webkit-scrollbar-thumb:hover{
  background: rgba(140, 140, 140, 0.5);
}
.cap-form::-webkit-scrollbar-thumb:active{
  background: rgba(200, 200, 200, 0.5);
}



