}





.cap-input-field {
  width: 100%;
  margin-bottom: 5%;
  display: flow;
}

.cap-input-field label {
  margin-right: 2%;
}

.cap-input-field input {
  flex-grow: 1;
  padding: 5px 5px 5px 5px;
  border: 2px solid #949494;
  box-sizing: border-box;
  border-radius: 5px;
  height: 43px;
}

.cap-instructions {
  display: flex;
  width: 100%;
  margin-bottom: 5%;
  flex-direction: column;
}

.cap-instructions label {
  margin-right: 2%;
}

.cap-instructions textarea {
  flex-grow: 1;
  border: 2px solid #949494;
  box-sizing: border-box;
  border-radius: 5px;
  padding: 10px 15px 80px 15px;
  width: 100%;
}

.cap-assignment-files {
  display: flex;
  flex-grow: 1;
  margin-bottom: 5%;
  align-items: center;
}

.cap-assignment-files label {
  margin-bottom: 1%;
}

.cap-assignment-files input {
  flex-grow: 1;
}

.cap-assignment-files input[type="file"]::file-selector-button {
  width: 118px;
  height: 34px;
  background-color: #4a7dfc;
  border-radius: 5px;
  color: white;
  border: none;
  margin-left: 10px;
}

.cap-assignment-info {
  display: flex;
  gap: 2%;
}

.cap-assignment-info label {
  margin-bottom: 1%;
  margin-right: 1%;
}

.cap-assignment-info input[type="date"] {
  padding: 5px 5px 5px 5px;
  flex-grow: 0.75;
  border: 2px solid #949494;
  box-sizing: border-box;
  border-radius: 5px;
  height: 43px;
}

.cap-assignment-info input[type="number"] {
  padding: 5px 5px 5px 5px;
  flex-grow: 0.75;
  border: 2px solid #949494;
  box-sizing: border-box;
  border-radius: 5px;
}

.cap-button {
  display: flex;
  justify-content: center;
}


.cap-altbutton {
  display: flex;
  justify-content: center;
  margin-top: 1%;
}

.cap-altbutton a {
  color: #5267a3;
}
