#teamTitle {
  margin-left: 5%;
}

#teamList {
  width: 100%;
}

#teamListItem {
  text-align: left;
  background: linear-gradient(
    to right,
    #7398ff 0%,
    #7398ff 1%,
    #eeeeee 1%,
    #eeeeee 100%
  );
  list-style-type: none;
  font-size: 0.75em;
  padding: 1%;
  margin: 1%;
  padding-top: 2%;
  padding-bottom: 2%;
  white-space: pre-line;
  font-weight: bold;
  padding-left: 2%;
}

#teamListItem:hover {
  background: linear-gradient(
    to right,
    #7398ff 0%,
    #7398ff 3%,
    #eeeeee 3%,
    #eeeeee 100%
  );
  padding-left: 4%;
}

#createTeamButton {
  display: flex;
  justify-content: center;
  margin-top: 3%;
}


